<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody v-if="coupons.length > 0">
        <tr v-for="coupon in coupons" :key="coupon.id">
          <td>{{coupon.title}}</td>
          <td>{{coupon.percent | percent}}</td>
          <td>{{coupon.due_date}}</td>
          <td>
            <span class="text-success" v-if="coupon.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group btn-group-sm" role="group" aria-label="...">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, coupon)"></button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelModal(coupon)"></button>
            </div>
          </td>
        </tr>
      </tbody>
      <tbody v-else><tr><td colspan="5" class="text-center"><strong></strong></td></tr></tbody>
    </table>

    <Pagination :pagination="pagination" @pageChange="getCoupons" />

    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="couponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="couponModalLabel">{{(isNew) ? "" : ""}}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                placeholder=""
                v-model="tempCoupon.title"
              />
            </div>
            <div class="form-group">
              <label for="code"></label>
              <input
                type="text"
                class="form-control"
                id="code"
                placeholder=""
                v-model="tempCoupon.code"
              />
            </div>
            <div class="form-group">
              <label for="dueDate"></label>
              <input
                type="date"
                class="form-control"
                id="dueDate"
                placeholder=""
                v-model="tempCoupon.due_date"
              />
            </div>
            <div class="form-group">
              <label for="percent"></label>
              <div class="input-group">
                <input
                  type="number"
                  class="form-control"
                  id="percent"
                  placeholder=""
                  v-model="tempCoupon.percent"
                />
                <div class="input-group-prepend">
                  <div class="input-group-text">%</div>
                </div>
              </div>
            </div>
            <div class="form-group form-check mb-0">
              <input
                type="checkbox"
                class="form-check-input"
                id="is_enabled"
                v-model="tempCoupon.is_enabled"
                :true-value="1"
                :false-value="0"
              />
              <label class="form-check-label" for="is_enabled"></label>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="updateCoupon"
            >{{(isNew) ? "" : ""}}</button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="delCouponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="delCouponModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="deleteCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "../Pagination";

export default {
  components: {
    Pagination
  },
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false
    };
  },
  methods: {
    getCoupons(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    updateCoupon() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = "post";
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(response => {
        $("#couponModal").modal("hide");
        vm.getCoupons();
        if (response.data.success) {
          this.$bus.$emit("showMessage", response.data.message, "success");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    deleteCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then(response => {
        $("#delCouponModal").modal("hide");
        vm.getCoupons();
        if (response.data.success) {
          this.$bus.$emit("showMessage", response.data.message, "success");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    openModal(isNew, coupon) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, coupon);
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    openDelModal(coupon) {
      this.tempCoupon = Object.assign({}, coupon);
      $("#delCouponModal").modal("show");
    }
  },
  created() {
    this.getCoupons();
  }
};
</script>



